.header {
	background : #001D3F;
	padding    : 10px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 20px 0;
	}
}

.header__container {
	max-width : 1440px;
	margin    : 0 auto;
	padding   : 0 10px;

	@media screen and (min-width : $break-desktop) {
		padding : 0 50px;
	}
}


.header__wrapper {
	display         : flex;
	align-items     : center;
	justify-content : space-between;
}

.header__socials {
	display : flex;
	gap     : 10px;

	@media screen and (min-width : $break-desktop) {
		gap : 30px;
	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : #006BE6;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	padding          : 0;
	position         : relative;
	width            : 45px;
	z-index          : 200;

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 5px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
		border-radius    : 10px;
	}

	&::before {
		top : -4px;
	}

	&::after {
		top : calc(100% - 1px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 5px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}

.nav {
	display         : flex;
	height          : 100dvh;
	align-items     : stretch;
	flex-flow       : column nowrap;
	justify-content : start;
	box-sizing      : border-box;
	position        : fixed;
	right           : 0;
	top             : 0;
	transition      : visibility .3s ease, opacity .3s ease;
	transform       : translateX(0%);
	width           : 100%;
	z-index         : 101;
	overflow-y      : overlay;
	overflow-x      : hidden;
	padding         : 12px 12px;
	border-radius   : 0;
	border          : 1px solid rgba(255, 255, 255, .05);
	visibility: hidden;
	opacity: 0;
	background: #16283c;

	&.active {
		visibility: visible;
		opacity: 1;
	}

	@media screen and (min-width : $break-desktop) {
		justify-content : center;
	}

}

.nav__list {
	padding-top    : 50px;
	display        : flex;
	flex-direction : column;
	@media screen and (min-width : $break-desktop) {
		align-items    : center;
	}
}

.nav__item {

	&:not(:last-child) {
		margin-bottom : 12px;
		@media screen and (min-width : $break-desktop) {
			margin-bottom : 25px;
		}
	}
}

.nav__link {
	font-weight: 400;
	font-size: 20px;
	text-transform: uppercase;
	color: #fff;
	text-decoration: none;
	transition:  color .3s ease;
	@media screen and (min-width : $break-desktop) {
		font-size: 32px;
	}

	&:hover {
		color: #006BE6;
	}
}


