  transition: visibility 0.3s ease, opacity 0.3s ease;
  transform: translateX(0%);
  width: 100%;
  z-index: 101;
  overflow-y: overlay;
  overflow-x: hidden;
  padding: 12px 12px;
  border-radius: 0;
  border: 1px solid rgba(255, 255, 255, 0.05);
