@font-face {
	font-family  : 'Istok';
	src          : url('../fonts/IstokWeb-Regular.woff2') format('woff2'),
	url('../fonts/IstokWeb-Regular.woff') format('woff'),
	url('../fonts/IstokWeb-Regular.ttf') format('truetype');
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}

@font-face {
	font-family  : 'Istok';
	src          : url('../fonts/IstokWeb-Bold.woff2') format('woff2'),
	url('../fonts/IstokWeb-Bold.woff') format('woff'),
	url('../fonts/IstokWeb-Bold.ttf') format('truetype');
	font-weight  : 700;
	font-style   : normal;
	font-display : swap;
}
