.page-wrapper {
	overflow : hidden;
}

.container {
	box-sizing : border-box;
	width      : (1032 + 15) * 1px;
	max-width  : 100%;
	margin     : auto;
	padding    : 0 7.5px;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-title {
	font-weight    : 700;
	font-size      : 20px;
	line-height    : 120%;
	text-transform : uppercase;
	color          : #FFF;
	border-bottom  : 1px solid #006BE6;
	padding-bottom : 10px;
	@media screen and (min-width : $break-desktop) {
		font-size      : 32px;
		padding-bottom : 10px;
	}
}

.hero {
	background-image  : url("../images/home/hero/hero-bg.jpg");
	background-size   : cover;
	background-repeat : no-repeat;
	height            : 100vh;
	padding-top       : 70px;

	@media screen and (min-width : $break-desktop) {
		padding-top : 87px;
	}
}

.hero--casino {
	background-image  : url("../images/home/hero/slot-bg.jpg");
	background-size   : cover;
	background-repeat : no-repeat;
	height            : 100vh;
}

.hero__wrapper {
	display        : flex;
	flex-direction : column;
	align-items    : center;
}


.hero__title {
	font-weight    : 700;
	font-size      : 22px;
	line-height    : 120%;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;
	margin-bottom  : 11px;

	@media screen and (min-width : $break-desktop) {
		font-size : 48px;
	}
}

.hero__text {
	font-weight : 400;
	font-size   : 14px;
	line-height : 120%;
	text-align  : center;
	color       : #A3CEFF;
	max-width   : 735px;

	@media screen and (min-width : $break-desktop) {
		font-size : 23px;
	}
}

.hotel-rooms {
	padding : 50px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 75px 0;
	}
}


.hotel-rooms__title {
	margin-bottom : 20px;
	text-align    : left;
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 30px;
	}
}

.hotel-rooms__grid {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	grid-gap              : 20px 20px;

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
		grid-gap              : 20px 70px;
	}
}

.hotel-rooms__item {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;;
	}
}


.hotel-rooms__img {
	border-radius : 10px;
	overflow      : hidden;
	margin-bottom : 10px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 14px;
	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}

.hotel-rooms__title {
	font-weight : 400;
	font-size   : 20px;
	line-height : 120%;
	color       : #FFF;

	@media screen and (min-width : $break-desktop) {
		font-size : 24px;
	}
}

.restaurants {
	background : #251A1A;

	@media screen and (min-width : $break-desktop) {
		padding : 30px 0 20px 0;
	}

	.section-title {
		margin-bottom : 30px;
	}
}

.restaurants__item {
	&:not(:last-child) {
		margin-bottom : 15px;

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 20px;
		}
	}
}

.restaurants__title {
	font-weight              : 400;
	font-size                : 20px;
	line-height              : 120%;
	text-decoration          : underline;
	text-decoration-skip-ink : none;
	color                    : #FFF;
	margin-bottom            : 10px;
	@media screen and (min-width : $break-desktop) {
		font-size : 24px;
	}
}

.restaurants__img {
	border-radius : 10px;
	overflow      : hidden;
	margin-bottom : 10px;

	@media screen and (min-width : $break-desktop) {

	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}

.restaurants__text {
	font-weight : 400;
	font-size   : 16px;
	line-height : 120%;
	color       : #FFF;
	@media screen and (min-width : $break-desktop) {
		font-size : 20px;
	}
}


.services {
	padding : 50px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0;
	}
}


.services__grid {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	grid-gap              : 15px;

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
		grid-gap              : 21px;
	}
}


.services-item {
	grid-column : 4 span;

	@media screen and (min-width : $break-desktop) {

	}
}

.services-item__name {
	font-weight   : 400;
	font-size     : 20px;
	line-height   : 120%;
	color         : #FFF;
	margin-bottom : 14px;
	@media screen and (min-width : $break-desktop) {
		font-size     : 24px;
		line-height   : 120%;
		margin-bottom : 10px;
	}
}

.services__img {
	border-radius : 10px;
	height        : 190px;
	margin-bottom : 10px;
	overflow      : hidden;
	@media screen and (min-width : $break-desktop) {
		height        : 253px;
		margin-bottom : 14px;
	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}

.services__text {
	font-weight : 400;
	font-size   : 16px;
	line-height : 120%;
	color       : #FFF;
	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 120%;
	}
}


.home-slots {
	background : #191919;
	padding    : 15px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 20px 0;
	}

	.section-title {
		margin-bottom : 20px;

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 30px;
		}
	}
}


.home-slots__img {
	border-radius : 10px;
	overflow      : hidden;
	margin-bottom : 10px;

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}

.home-slots__text {
	font-weight   : 400;
	font-size     : 16px;
	line-height   : 120%;
	color         : #FFF;
	margin-bottom : 10px;
	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 120%;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 15px;
		}
	}
}

.alert {
	font-weight   : 400;
	font-size     : 16px;
	line-height   : 120%;
	color         : #FFF;
	background    : #4C0A0A;
	border        : 1px solid #006BE6;
	border-radius : 10px;
	padding       : 5px 10px;
	display       : inline-block;
	@media screen and (min-width : $break-desktop) {
		font-size : 20px;
	}

	a {
		color           : #FFF;
		text-decoration : none;

		&:hover {
			color : #FFF;
		}
	}
}

.footer {
	background : #16283C;
	padding    : 34px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 20px 0;
	}
}


.footer__left {
	display        : flex;
	flex-direction : column;
	align-items    : center;
	@media screen and (min-width : $break-desktop) {
		display        : flex;
		flex-direction : row;
		gap            : 153px;
		align-items    : unset;
	}
}

.footer__title {
	font-weight    : 700;
	font-size      : 20px;
	line-height    : 120%;
	text-transform : uppercase;
	color          : #FFF;
	margin-bottom  : 10px;
	text-align     : center;
	@media screen and (min-width : $break-desktop) {
		text-align : left;
	}
}

.footer__links {
	display        : flex;
	align-items    : center;
	flex-direction : column;
	gap            : 10px;
	margin         : 40px 0;
	@media screen and (min-width : $break-desktop) {
		text-align     : left;
		flex-direction : row;
		margin         : 10px 0;
	}

	a {
		font-weight     : 700;
		font-size       : 20px;
		line-height     : 120%;
		text-align      : center;
		color           : #FFF;
		text-decoration : none;

		&:hover {
			color : #FFF;
		}
	}
}

.footer__right {
	display        : flex;
	align-items    : center;
	flex-direction : column;
	@media screen and (min-width : $break-desktop) {
		display        : flex;
		gap            : 206px;
		align-items    : center;
		flex-direction : row;
	}
}

.footer__text {
	font-weight   : 400;
	font-size     : 16px;
	line-height   : 120%;
	color         : #FFF;
	margin-bottom : 10px;
	text-align    : center;
	margin-top    : 40px;
	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 120%;
		text-align  : left;
		margin-top  : 0;
	}
}

.footer__img {
	text-align      : left;
	display         : flex;
	justify-content : center;
	align-items     : center;
	margin-top      : 40px;
	@media screen and (min-width : $break-desktop) {
		justify-content : flex-start;
		margin-top      : 0;
	}
}

.casino {
	background : #191919;
	padding    : 15px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 30px 0;
	}
}

.casino__title {
	font-weight    : 700;
	font-size      : 20px;
	line-height    : 120%;
	text-transform : uppercase;
	color          : #FFF;

	@media screen and (min-width : $break-desktop) {
		font-size : 32px;
	}
}

.casino__text {
	font-weight   : 400;
	font-size     : 16px;
	line-height   : 120%;
	color         : #FFF;
	margin-bottom : 15px;
	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		margin-bottom : 20px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 15px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 20px;
			}
		}
	}
}

.schedule {
	display               : grid;
	grid-template-columns : repeat(8, 1fr);
	grid-gap              : 30px 15px;

	@media screen and (min-width : 844px) {
		max-width : 834px;
		grid-gap  : 40px;
	}
}

.schedule__item {
	grid-column : 4 span;
	font-weight : 400;
	font-size   : 16px;
	line-height : 120%;
	text-align  : center;
	color       : #A3CEFF;

	@media screen and (min-width : 844px) {
		grid-column : 2 span;
		font-size   : 20px;
	}

	div {
		&:first-child {
			text-align : center;
		}
	}
}

.system {
	padding : 10px 0 50px 0;
	@media screen and (min-width : $break-desktop) {
		padding : 20px 0 100px 0;
	}
}
.list-reset {
	list-style: none;
	margin: 0;
	padding: 0;
}

.system__title {
	margin-bottom : 20px;
}

.system__text {
	font-weight : 400;
	font-size   : 16px;
	line-height : 120%;
	color       : #FFF;

	@media screen and (min-width : $break-desktop) {
		font-size : 20px;

	}

	&:not(:last-child) {
		margin-bottom : 20px;
	}

	a {
		color           : #FFF;
		text-decoration : none;

		&:hover {
			color : #FFF;
		}
	}
}
